<template>
  <div class="card">
    <div class="card-body">
      <h3 class="mb-4">📍 打卡与留言</h3>
      
      <!-- 打卡按钮 -->
      <div class="checkin-section mb-4">
        <button
          class="btn btn-success btn-lg"
          @click="handleCheckin"
          :disabled="checking || hasCheckedIn"
        >
          <span v-if="checking" class="spinner-border spinner-border-sm me-2"></span>
          {{ checkinButtonText }}
        </button>
        <p v-if="hasCheckedIn" class="text-success mt-2 mb-0">
          ✅ 您已打卡成功！
        </p>
      </div>
      
      <!-- 发表评论表单 -->
      <div class="comment-form mb-4">
        <h5 class="mb-3">发表评论</h5>
        <form @submit.prevent="handleSubmit">
          <div class="mb-3">
            <textarea
              v-model="formData.content"
              class="form-control"
              rows="4"
              placeholder="分享你的旅行故事..."
              required
            ></textarea>
          </div>
          
          <div class="row mb-3">
            <div class="col-md-6 mb-2">
              <label class="form-label">上传图片</label>
              <input
                type="file"
                class="form-control"
                accept="image/*"
                @change="handleImageChange"
              />
            </div>
            <div class="col-md-6 mb-2">
              <label class="form-label">上传视频</label>
              <input
                type="file"
                class="form-control"
                accept="video/*"
                @change="handleVideoChange"
              />
            </div>
          </div>
          
          <button
            type="submit"
            class="btn btn-primary"
            :disabled="submitting"
          >
            <span v-if="submitting" class="spinner-border spinner-border-sm me-2"></span>
            {{ submitting ? '发布中...' : '发布评论' }}
          </button>
        </form>
      </div>
      
      <!-- 评论列表 -->
      <div class="comment-list">
        <div class="d-flex justify-content-between align-items-center mb-3">
          <h5 class="mb-0">评论列表 ({{ comments.length }}条)</h5>
          <!-- 管理评论模式切换按钮 -->
          <button
            v-if="hasManageableComments"
            class="btn btn-sm"
            :class="isManageMode ? 'btn-secondary' : 'btn-outline-secondary'"
            @click="toggleManageMode"
            title="管理评论"
          >
            {{ isManageMode ? '完成管理' : '管理评论' }}
          </button>
        </div>
        
        <div v-if="comments.length === 0" class="text-center text-muted py-4">
          暂无评论，快来留下第一条评论吧！
        </div>
        
        <div
          v-for="comment in comments"
          :key="comment.id"
          class="comment-item"
        >
          <div class="d-flex align-items-start">
            <img
              :src="getAvatarUrl(comment.user.profile?.avatar_url)"
              class="rounded-circle me-3"
              width="48"
              height="48"
              alt="avatar"
              @error="(e) => { console.log('头像加载失败:', comment.user); e.target.src='/static/images/default_avatar.png'; }"
            />
            <div class="flex-grow-1">
              <div class="d-flex justify-content-between align-items-center mb-2">
                <strong>{{ comment.user.username }}</strong>
                <div class="d-flex gap-2 align-items-center">
                  <small class="text-muted">{{ formatDate(comment.timestamp) }}</small>
                  <!-- 仅在管理模式下显示的操作按钮 -->
                  <template v-if="isManageMode">
                    <!-- 编辑模式按钮 -->
                    <button
                      v-if="comment.can_delete && !editingComments[comment.id]"
                      class="btn btn-sm btn-outline-primary"
                      @click="startEditing(comment.id, comment.content)"
                      title="编辑评论"
                    >
                      ✏️
                    </button>
                    <!-- 保存按钮 -->
                    <button
                      v-if="editingComments[comment.id]"
                      class="btn btn-sm btn-success"
                      @click="handleSaveComment(comment.id)"
                      title="保存修改"
                    >
                      ✓
                    </button>
                    <!-- 添加/替换图片按钮（仅在编辑模式下显示） -->
                    <button
                      v-if="canAddImage(comment) && editingComments[comment.id]"
                      class="btn btn-sm btn-outline-success"
                      @click="handleAddImage(comment.id)"
                      :title="comment.image ? '替换图片' : '添加图片'"
                    >
                      📷
                    </button>
                    <!-- 取消编辑按钮 -->
                    <button
                      v-if="editingComments[comment.id]"
                      class="btn btn-sm btn-outline-secondary"
                      @click="cancelEditing(comment.id)"
                      title="取消"
                    >
                      ✕
                    </button>
                    <button
                      v-if="comment.can_delete"
                      class="btn btn-sm btn-outline-danger"
                      @click="handleDelete(comment.id)"
                      title="删除这条评论"
                    >
                      🗑️
                    </button>
                  </template>
                </div>
              </div>
              
              <!-- 编辑模式：显示编辑框 -->
              <div v-if="editingComments[comment.id]" class="mb-2">
                <textarea
                  v-model="editingComments[comment.id].content"
                  class="form-control"
                  rows="3"
                  placeholder="编辑评论内容..."
                ></textarea>
              </div>
              <!-- 普通模式：显示原内容 -->
              <p v-else class="mb-2">{{ comment.content }}</p>
              
              <!-- 图片 -->
              <div v-if="comment.image" class="mb-2 comment-media">
                <img
                  :src="comment.image"
                  class="img-fluid rounded"
                  style="cursor: pointer;"
                  @click="showImageModal(comment.image)"
                  alt="评论图片"
                />
              </div>
              
              <!-- 视频 -->
              <div v-if="comment.video" class="mb-2 comment-media">
                <video
                  :src="comment.video"
                  controls
                  class="rounded"
                >
                  您的浏览器不支持视频播放
                </video>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 图片放大模态框 -->
    <div v-if="showModal" class="image-modal" @click="closeImageModal">
      <div class="modal-content" @click.stop>
        <button class="close-button" @click="closeImageModal">✕</button>
        <img :src="modalImageUrl" alt="放大图片" />
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'

export default {
  name: 'CommentSection',
  
  props: {
    comments: {
      type: Array,
      default: () => []
    },
    isAdmin: {
      type: Boolean,
      default: false
    },
    hasCheckedIn: {
      type: Boolean,
      default: false
    },
    getAvatarUrl: {
      type: Function,
      required: true
    }
  },
  
  emits: ['checkin', 'submit-comment', 'delete-comment', 'add-image', 'update-comment'],
  
  setup(props, { emit }) {
    const checking = ref(false)
    const submitting = ref(false)
    const editingComments = ref({})  // 跟踪哪些评论处于编辑模式
    const isManageMode = ref(false)  // 管理评论模式
    const showModal = ref(false)  // 控制图片放大模态框显示
    const modalImageUrl = ref('')  // 当前要展示的图片URL
    
    const formData = ref({
      content: '',
      image: null,
      video: null
    })
    
    // 计算是否有可管理的评论
    const hasManageableComments = computed(() => {
      return props.comments.some(comment => comment.can_delete)
    })
    
    // 切换管理模式
    const toggleManageMode = () => {
      isManageMode.value = !isManageMode.value
      if (!isManageMode.value) {
        // 退出管理模式时，清除所有编辑状态
        editingComments.value = {}
      }
    }
    
    const checkinButtonText = computed(() => {
      if (props.hasCheckedIn) return '✓ 已打卡'
      if (checking.value) return '打卡中...'
      return '📍 我来过这里'
    })
    
    const handleCheckin = async () => {
      checking.value = true
      try {
        await emit('checkin')
      } finally {
        setTimeout(() => {
          checking.value = false
        }, 500)
      }
    }
    
    const handleImageChange = (event) => {
      formData.value.image = event.target.files[0]
    }
    
    const handleVideoChange = (event) => {
      formData.value.video = event.target.files[0]
    }
    
    const handleSubmit = async () => {
      submitting.value = true
      try {
        await emit('submit-comment', {
          content: formData.value.content,
          image: formData.value.image,
          video: formData.value.video
        })
        
        // 重置表单
        formData.value = {
          content: '',
          image: null,
          video: null
        }
      } finally {
        submitting.value = false
      }
    }
    
    const handleDelete = (commentId) => {
      if (window.confirm('🗑️ 确定要删除这条评论吗？此操作无法撤销。')) {
        emit('delete-comment', commentId)
      }
    }
    
    const formatDate = (dateStr) => {
      const date = new Date(dateStr)
      const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0')
      const day = String(date.getDate()).padStart(2, '0')
      const hours = String(date.getHours()).padStart(2, '0')
      const minutes = String(date.getMinutes()).padStart(2, '0')
      
      return `${year}-${month}-${day} ${hours}:${minutes}`
    }
    
    const showImageModal = (url) => {
      modalImageUrl.value = url
      showModal.value = true
    }
    
    const closeImageModal = () => {
      showModal.value = false
      modalImageUrl.value = ''
    }
    
    const canAddImage = (comment) => {
      // 判断是否可以添加/替换图片：
      // 1. 评论没有视频（有视频就不能添加图片）
      // 2. 评论作者是当前用户（通过can_delete判断）
      // 3. 允许替换已有图片
      return !comment.video && comment.can_delete
    }
    
    const startEditing = (commentId, originalContent) => {
      // 保存原始内容以便取消时恢复
      editingComments.value[commentId] = {
        isEditing: true,
        content: originalContent
      }
    }
    
    const cancelEditing = (commentId) => {
      editingComments.value[commentId] = false
    }
    
    const handleSaveComment = (commentId) => {
      const editedContent = editingComments.value[commentId]?.content
      if (editedContent && editedContent.trim()) {
        emit('update-comment', {
          commentId,
          content: editedContent.trim()
        })
        editingComments.value[commentId] = false
      }
    }
    
    const handleAddImage = (commentId) => {
      const input = document.createElement('input')
      input.type = 'file'
      input.accept = 'image/*'
      input.onchange = (e) => {
        const file = e.target.files[0]
        if (file) {
          emit('add-image', { commentId, file })
          // 添加图片后退出编辑模式
          editingComments.value[commentId] = false
        }
      }
      input.click()
    }
    
    return {
      checking,
      submitting,
      formData,
      editingComments,
      isManageMode,
      hasManageableComments,
      checkinButtonText,
      handleCheckin,
      handleImageChange,
      handleVideoChange,
      handleSubmit,
      handleDelete,
      formatDate,
      showImageModal,
      closeImageModal,
      canAddImage,
      startEditing,
      cancelEditing,
      handleAddImage,
      handleSaveComment,
      toggleManageMode,
      showModal,
      modalImageUrl
    }
  }
}
</script>

